import React, { useState } from "react";
import Box from "@mui/material/Box";
// import Button from "@mui/material/Button";
import Tooltip from "@mui/material/Tooltip";
import TextField from "@mui/material/TextField";
import LoadingButton from "@mui/lab/LoadingButton"
// import { FaUserEdit } from "react-icons/fa";
import { useFormik } from "formik";
import * as yup from "yup";
import axios from "axios";
import { useDispatch } from "react-redux";
import { curUserFun } from "../redux/actions/index";
import appSetting from '../appSetting/appSetting'
import MuiSnacks from "./MuiSnacks";


function EditProfileComp({ curUser, setCurUser }) {
    const LS = JSON.parse(localStorage.getItem("me"))
    const [loadBtn, setLoadBtn] = useState(false);
    const [openSnack, setOpenSnack] = useState("");
    const [severity, setSeverity] = useState("");
    // const handleClickOpen = () => {
    //     setOpen(true);
    // };
    const dispatch = useDispatch()

    // const handleClose = () => {
    //     setOpen(false);
    // };
    const formik = useFormik({
        initialValues: {
            id: curUser?._id,
            fname: curUser?.fname,
            lname: curUser?.lname,
            fatherName: curUser?.fatherName,
            age: curUser?.age,
            phone: curUser?.phone,
        },
        validateOnChange: true,

        validationSchema: yup.object().shape({
            fname: yup.string()
                .max(12, "Plese Enter a Name less then 12 Characters")
                .required("Please Enter First Name"),
            lname: yup.string()
                .max(12, "Plese Enter a Name less then 12 Characters")
                .required("Please Enter Last Name"),
            fatherName: yup.string()
                .max(24, "Plese Enter a Name less then 24 Characters")
                .required("Please Enter Your Father's Name"),
            age: yup.number().required("It is not a good habit to hide your age.")
                .positive("That doesn't look like a human's age.")
                .integer("Don't use decimal point")
                .min(10, "You are not egligible for this school, you are a baby,")
                .max(199, "A noble human cannot live so long"),
            phone: yup.number()
                .typeError("That doesn't look like a phone number")
                .positive("A phone number can't start with a minus")
                .integer("A phone number can't include a decimal point")
                .min(3000000000, "It doesn't looks like a phone number")
                .max(3499999999, "It doesn't looks like a phone number")
                .required('A phone number is required')
        }),
        onSubmit: async (values) => {
            try {
                setLoadBtn(true);
                const res = await axios.post(`${appSetting.severHostedUrl}/user/edit-profile`, values, { headers: { Authentication: `Bearer ${LS?.token}` } });
                if (res) {
                    setLoadBtn(false);
                    setCurUser(res.data.updated)
                    dispatch(curUserFun(res.data.updated))
                    setOpenSnack(res.data.message)
                    setSeverity("success")
                }
            } catch (error) {
                setOpenSnack(error?.response?.data?.error)
                setSeverity("error")
                setLoadBtn(false);
            }
        }

    })
    return (
        <Box width="100%" minHeight="88vh"
            sx={{ backgroundColor: "#f6f6f6" }}
            display="flex" justifyContent="center" alignItems="flex-start"
        >
            {openSnack ? <MuiSnacks openSnack={openSnack} severity={severity} text={openSnack} setOpenSnack={setOpenSnack} /> : ""}
            <Box
                width={{ xs: "90%", sm: "600px" }}
                mx={2} mt={4} px={3} py={"50px"}
                sx={{ backgroundColor: "#fff", boxShadow: 2 }}
            >
                <form onSubmit={formik.handleSubmit}>
                    <TextField
                        autoFocus
                        margin="dense"
                        name="fname"
                        label="First Name"
                        type="text"
                        variant="outlined"
                        value={formik.values.fname}
                        onChange={formik.handleChange("fname")}
                        autoComplete="off"
                        fullWidth
                        color="success"
                    />
                    {formik.errors.fname && formik.touched.fname && (
                        <p style={{ color: "red", marginLeft: "5px" }}>
                            {formik.errors.fname}
                        </p>
                    )}
                    <TextField
                        margin="dense"
                        name="lname"
                        label="Last Name"
                        type="text"
                        variant="outlined"
                        value={formik.values.lname}
                        onChange={formik.handleChange("lname")}
                        autoComplete="off"
                        fullWidth
                        color="success"
                    />
                    {formik.errors.lname && formik.touched.lname && (
                        <p style={{ color: "red", marginLeft: "5px" }}>
                            {formik.errors.lname}
                        </p>
                    )}
                    <TextField
                        margin="dense"
                        name="fatherName"
                        label="Son of"
                        type="text"
                        variant="outlined"
                        value={formik.values.fatherName}
                        onChange={formik.handleChange("fatherName")}
                        autoComplete="off"
                        fullWidth
                        color="success"
                    />
                    {formik.errors.fatherName && formik.touched.fatherName && (
                        <p style={{ color: "red", marginLeft: "5px" }}>
                            {formik.errors.fatherName}
                        </p>
                    )}
                    <TextField
                        margin="dense"
                        name="age"
                        label="Age"
                        type="number"
                        variant="outlined"
                        value={formik.values.age}
                        onChange={formik.handleChange("age")}
                        autoComplete="off"
                        fullWidth
                        color="success"
                    // required
                    />
                    {formik.errors.age && formik.touched.age && (
                        <p style={{ color: "red", marginLeft: "5px" }}>
                            {formik.errors.age}
                        </p>
                    )}
                    <Tooltip title="Enter Phone Number Escaped 0" arrow>
                        <TextField
                            margin="dense"
                            name="phone"
                            label="Contact No..."
                            type="number"
                            variant="outlined"
                            value={formik.values.phone}
                            onChange={formik.handleChange("phone")}
                            autoComplete="off"
                            fullWidth
                            color="success"
                        // required
                        />
                    </Tooltip>
                    {formik.errors.phone && formik.touched.phone && (
                        <p style={{ color: "red", marginLeft: "5px" }}>
                            {formik.errors.phone}
                        </p>
                    )}
                    <LoadingButton
                        size="large"
                        type="submit"
                        mt={2}
                        variant="contained"
                        color="success"
                        fullWidth
                        loading={loadBtn}
                        sx={{ py: loadBtn ? 2.7 : 1, mt: 4 }}
                    >
                        {loadBtn ? "" : "Save Changes"}
                    </LoadingButton>
                </form>
            </Box>
        </Box>
    );
}
export default EditProfileComp